@import url(https://fonts.googleapis.com/css2?family=Karla:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{padding:0;margin:0;box-sizing:border-box}body{font-family:'Karla', sans-serif;background:linear-gradient(-50deg, #7F96FF 0%, #1B998B 90%)}.cardContainer{width:800px;height:100%;display:flex;flex-wrap:wrap;background:transparent;margin:0 auto}@media screen and (max-width: 680px){.cardContainer{width:300px}}.card{margin:1rem auto;background:transparent;perspective:1000px;height:250px;width:250px;border-radius:2rem}.flip-card-inner{border-radius:2rem;position:relative;width:100%;height:100%;text-align:center;transition:transform 0.6s;transform-style:preserve-3d;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.card:hover .flip-card-inner{transform:rotateY(180deg)}.cardFront{border-radius:2rem;background-size:cover;display:flex;align-items:start;justify-content:start;position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden}.tag{margin:1rem;padding:.5rem 1rem;color:#F1DEDE;border-radius:2rem;font-size:.8rem}.new-recipe{background-color:#F05D5E}.no-sugar{background-color:#72195A}@media screen and (max-width: 680px){.tag{font-size:.8rem}}.cardBack{background-color:#F05D5E;color:#F1DEDE;border-radius:2rem;position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;transform:rotateY(-180deg);display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.score{width:100px;display:inline-flex;justify-content:space-evenly;font-size:.8rem}.cardTitle{font-family:'Dancing Script', cursive}.github{width:3rem;height:4rem;background-color:rgba(107,105,105,0.239);display:flex;align-items:center;justify-content:center;border-radius:1rem;position:fixed;top:-.6rem;left:1rem}.github:hover{cursor:pointer;background-color:rgba(107,105,105,0.557);box-shadow:0 0 10px 1px #d1d1d1}.github>a{font-size:1.9rem;color:#000}.github>a:visited{color:#000}

